













































import {Component, Prop, Vue, Provide, Emit} from 'vue-property-decorator';
import {
    State,
    Getter,
    Action,
    Mutation,
    namespace
} from 'vuex-class'
import {router} from '../../main';
import {Message} from "element-ui";

@Component({
    components: {},
})

export default class EditForm extends Vue {

    @State('auth') auth
    @Action('auth/fetchInfo') fetchInfo

    @Prop() friendToEdit

    @Provide() infoForm = {
        name: this.friendToEdit.name,
        sex: this.friendToEdit.sex,
        tags: this.friendToEdit.tags.split(' ')
    }
    @Provide() rules = {
        name: [
            {required: true, message: '请输入名字', trigger: 'blur'},
        ]
    }

    @Provide() inputVisible = false
    @Provide() inputValue = ''

    @Action('auth/updateFriend') updateFriend

    @Emit()
    handleTagClose(tag) {
        this.infoForm.tags.splice(this.infoForm.tags.indexOf(tag), 1);
    }

    @Emit()
    showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
            this.$refs.saveTagInput.$refs.input.focus();
        });
    }

    @Emit()
    handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {
            this.infoForm.tags.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = '';
    }

    @Emit()
    handleClose() {
        this.$modal.hide('manage-friend-modal');
    }

    @Emit()
    handleConfirm() {
        this.updateFriend({
            info: this.infoForm,
            onSuccess: (success) => {
                Message.success(success)
                this.$modal.hide('manage-friend-modal')
            },
            onError: (error) => {
                Message.error(error)
            }
        })
    }

}




