




















































import {Component, Prop, Vue, Provide, Emit} from 'vue-property-decorator';
import {
    State,
    Getter,
    Action,
    Mutation,
    namespace
} from 'vuex-class'
import {router} from '../../main';
import {Message} from "element-ui";

@Component({
    components: {},
})

export default class AddFriendModal extends Vue {

    @Action('auth/addNewFriend') addNewFriend

    @Provide() infoForm = {
        name: '',
        sex: '男',
        tags: ['好友']
    }
    @Provide() rules = {
        name: [
            {required: true, message: '请输入名字', trigger: 'blur'},
        ]
    }

    @Provide() inputVisible = false
    @Provide() inputValue = ''

    @Emit()
    handleTagClose(tag) {
        this.infoForm.tags.splice(this.infoForm.tags.indexOf(tag), 1);
    }

    @Emit()
    showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
            this.$refs.saveTagInput.$refs.input.focus();
        });
    }

    @Emit()
    handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {
            this.infoForm.tags.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = '';
    }

    @Emit()
    handleClose() {
        this.$modal.hide('add-friend-modal')
    }

    @Emit()
    handleConfirm() {
        this.addNewFriend({
            info: this.infoForm,
            onSuccess: (success) => {
                Message.success(success)
                this.$modal.hide('add-friend-modal')
                this.infoForm = {
                    name: '',
                    sex: '男',
                    tags: ['好友']
                }
            },
            onError: (error) => {
                Message.error(error)
            }
        })
    }

}



